.pic-artist {
  width: 700px;
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 5px;
  margin: 0 auto;
}
.pic-artist:hover {
  box-shadow: 0 0 7px 5px rgba(168, 191, 199, 0.5);
  transition: ease 1s;
}
