.item {
  padding: 10px;
  margin: 10px;
}
.pic {
  border: solid 1px #f4f4f4;
  width: 100%;
  object-fit: cover;
}
/* .item:hover {
  box-shadow: 0 2px 6px 0 rgba(32, 33, 36, 0.28);
  border-color: rgba(223, 225, 229, 0);
  text-decoration: none;
} */
.item:hover {
  box-shadow: 0 0 2px 1px rgba(168, 191, 199, 0.5);
  transform: scale(1.1);
  transition: ease 0.5s;
  cursor: pointer;
}
.gallery {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: auto;
  width: 100%;
}
